<script>
import {
  pubFundProduct,
  showFundProduct,
  allProjectOrFund,
  delFundFile,
  pubFundList,
  delPubHis
} from '../../config/api'
import progressBar from '../../components/progressbar.vue'
import {
  dialogConfirm,
  dialogConfirmHasbtn
} from '../../config/mUtils'
export default {
  data() {
    return {
      keyWord: '',
      startTime: '',
      endTime: '',
      data: {
        fid: '',
        name: '',
        direction: '',
        describe: '',
        status: '',
        contributeSize: null,
        startMoney: null,
        appendMoney: null,
        duration: '',
        manageFees: null,
      },
      id: null, //当前的id
      isGetFileList: [], //存放网上的文件的数组
      delfileidList: [], //需要被删除的文件id
      isLocalFileList: [], //存放本地展示的文件的数组
      publishHistoryList: [], //存放发布历史列表数据的数组
      allProjectOrFundList: [], //关联基金的下拉列表
      staus: [{
        id: '1',
        name: '在售'
      }, {
        id: '0',
        name: '售罄'
      }]
    }
  },
  components: {
    progressBar
  },
  mounted() {
    this.getPublishHistoryList();
    this.getAllProjectOrFund()
  },
  computed: {
    submitOk: function() {
      if (this.data.fid != '' && this.data.name != '' && this.data.direction != '' && this.data.describe != '' && this.data.contributeSize != null) {
        return true
      } else {
        return false
      }
    }
  },
  methods: {
    init() {
      this.getPublishHistoryList();
      this.startTime = '',
        this.endTime = '',
        this.data = {
          fid: '',
          name: '',
          direction: '',
          describe: '',
          status: '',
          contributeSize: null,
          startMoney: null,
          appendMoney: null,
          duration: '',
          manageFees: null,
        };
      this.id = null;
      this.isGetFileList = [];
      this.isLocalFileList = [];
    },
    toNewFund() {
      dialogConfirmHasbtn('确定要离开编辑页面吗？').then(() => {
        this.init();
      }).catch(() => {});
    },
    //获取关联基金的下拉数据
    getAllProjectOrFund() {
      allProjectOrFund({
        type: 2
      }).then(res => {
        // console.log(res);
        if (res.data.msgNo == 1) {
          this.allProjectOrFundList = res.data.payload.data;
          // if (this.allProjectOrFundList.length != 0) {
          //   this.data.fid = this.allProjectOrFundList[0].id;
          // }

        }
      })
    },
    //获取要上传的文件
    addFile($event) {
      for (let val of $event.target.files) {
        //console.log(val);
        // this.isLocalFileList.push(val)
        this.isLocalFileList.push({
          name: val.name,
          path: URL.createObjectURL(val),
          file: val
        });
      }
    },
    //获取要替换的文件
    changeFile($event) {},

    //发布或修改基金信息 （不传id就是新建，传id就是修改）
    toPunlishFundProduct() {
      if (this.submitOk) {
        // if (this.startTime == '' || this.startTime == null || this.startTime == undefined) {
        //   this.startTime = '0';
        // }
        // if (this.endTime == '' || this.endTime == null || this.endTime == undefined) {
        //   this.endTime = '0';
        // }
        this.data.duration = this.startTime + '+' + this.endTime
        if (this.id != null) {
          this.data.id = JSON.parse(JSON.stringify(this.id));
        }
        // console.log(this.data);
        //删除远程文件
        if (this.delfileidList.length != 0) {
          this.delfileidList.forEach(item => {
            delFundFile({
              file_id: item
            }).then(res => {})
          })
        }
        pubFundProduct({
          data: this.data
        }, this.isLocalFileList).then(res => {
          //console.log(res);
          if (res.data.msgNo == 1) {
            if (JSON.parse(JSON.stringify(this.id)) != null) {
              dialogConfirm('success', '修改成功')
            } else {
              dialogConfirm('success', '发布成功')
            }
            this.init();
          } else {
            dialogConfirm('info', res.data.message);
          }
        })
      } else {
        dialogConfirm('info', '您还有必填项没有填写！')
      }

    },
    //删除已上传的基金文件
    toDelFundFile(id, type) {
      this.isLocalFileList.splice(id, 1);
    },
    toDelGetFundFile(index, id, type) {
      this.delfileidList.push(id);
      this.isGetFileList.splice(index, 1);
    },
    //获取基金产品发布的历史列表
    getPublishHistoryList() {
      let params = {};
      if (this.keyWord != '') {
        params.search = this.keyWord;
      }
      pubFundList(params).then(res => {
        // console.log(res);
        if (res.data.msgNo == 1) {
          this.publishHistoryList = res.data.payload
        }
      })
    },
    //获取某条基金产品的信息
    toShowFundProduct(id) {
      this.delfileidList = [];
      this.isLocalFileList = [];
      showFundProduct({
        id: id
      }).then(res => {
        // console.log(res);
        if (res.data.msgNo == 1) {
          let data = res.data.payload;
          // console.log(data);
          if (data.data.duration != '') {
            let arr = data.data.duration.split('+');
            this.startTime = arr[0];
            this.endTime = arr[1];
          }
          this.data.fid = data.data.fid
          this.data.name = data.data.name
          this.data.direction = data.data.direction
          this.data.describe = data.data.describe
          this.data.contributeSize = data.data.contributeSize
          this.data.startMoney = data.data.startMoney
          this.data.appendMoney = data.data.appendMoney
          this.data.manageFees = data.data.manageFees
          this.data.status = data.data.status
          this.data.status = this.data.status.toString();

          this.isGetFileList = data.file
          this.id = data.data.id;
        }
      })
    },
    //删除某条基金产品
    todDelFundProduct(id, index) {
      dialogConfirmHasbtn('确定要删除该发布历史？').then(() => {
        delPubHis({
          id: id
        }).then(res => {
          if (res.data.msgNo == 1) {
            this.publishHistoryList.splice(index, 1);
            if (this.id == id) {
              this.init();
            }
            dialogConfirm('success', '删除成功')
          }
        })
      }).catch(() => {});
    },
    cutString(str, len) {
      // console.log(typeof str);
      let type = typeof str;
      if (type == 'number') {
        return str;
      }
      //length属性读出来的汉字长度为1 
      if (str.length * 2 <= len) {
        return str;
      }
      var strlen = 0;
      var s = "";
      for (var i = 0; i < str.length; i++) {
        s = s + str.charAt(i);
        if (str.charCodeAt(i) > 128) {
          strlen = strlen + 2;
          if (strlen >= len) {
            return s.substring(0, s.length - 1) + "...";
          }
        } else {
          strlen = strlen + 1;
          if (strlen >= len) {
            return s.substring(0, s.length - 2) + "...";
          }
        }
      }
      return s;
    }
  }
};

</script>
