  background-color: #3C98E8;
  position: absolute;
  left: -11px;
  top: 5px;
}

.item-text {
  font-size: 14px;
  color: #282828;
  margin: 4px 0 12px 0;
  font-weight: 500;
  max-height: 40px;
  line-height: 20px;
  overflow: hidden;
}

.content-confirm {
  margin-left: 106px;
  margin-top: 30px;
}

@media (max-width: 1350px) {
  .content-body-left-box {
    right: 280px;
  }
  .content-body-right-box {
    width: 280px;
  }
}

</style>



